body, html {
    width: 100%;
    height: 100%;
    display: flex;
    background-color: #333333;
    box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
    color: #ffffff;
    font-family: Avenir, Georgia, 'Times New Roman', Times, serif;
    /* background-image: url('./images/bgLight.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed; */
}

header {
    background-color: #00000050;
    color: #cccccc;
}

#headLine {
    color: #cccccc;
    border-left: 2px solid #cccccc;
    border-bottom: 2px solid #cccccc;
    padding: 0px 0px 10px 10px;
}

.card {
    color: #000000;
    margin-bottom: 10px;
}

.fa-primary {
    color: #0275d8;
}

#blankCard {
    border: 5px dashed #000000;
    opacity: 0.5;
}

.modal {
    color: #000000
}

.scrollable {
    overflow: scroll;
}

.scrollbar-hidden {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.scrollbar-hidden::-webkit-scrollbar { 
    display: none;
}

footer {
    color: #cccccc;
    text-align: center;
    height: 50px;
    line-height: 50px;
}

.loadingOverlay {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    background-color: #00000099;
    height: 100vh;
    width: 100vw;
    margin: 0;
    z-index: 100;
}

.loadingSpan {
    color: #000000;
    background-color: #ffffff;
    height: 120px;
    width: 120px;
    border-radius: 25px;
    text-align: center;
    padding: 10px;
}

.fa-black {
    color: #000000;
}